import React, { useEffect } from "react";
import { Button, Input } from "../../components";
import styles from "./loginPage.module.css";
import { Link, useNavigate } from "react-router-dom";
import { REGISTRATION_ROUTE, USERS_ROUTE } from "../../utils/constans";
import { useForm } from "react-hook-form";
import { useDispatch, useSelector } from "react-redux";
import { registrationUser } from "../../store/auth/authActionCreators";

export const LoginPage = () => {
  const { auth } = useSelector((state) => state.auth);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm();

  useEffect(() => {
    if (auth) {
      navigate(USERS_ROUTE);
    }
  }, [auth]);

  const onSubmit = (formData) => {
    dispatch(registrationUser(formData));
  };

  const nav = (
    <>
      <span>  ?</span>{" "}
      <Link to={REGISTRATION_ROUTE}></Link>
    </>
  );

  return (
    <div className={styles.wrapper}>
      <div className={styles.form_wrapper}>
        <h3 className={styles.title}></h3>
        <form onSubmit={handleSubmit(onSubmit)} className={styles.form}>
          <div className={styles.inputs}>
            <Input
              title=" "
              type="email"
              {...register("email", {
                required: { value: true, message: " email" },
                pattern: {
                  value:
                    /^[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?$/,
                  message: " email ",
                },
              })}
              placeholder="Email"
              error={errors.email}
            />
            <Input
              title=""
              type="password"
              hasIconHiding
              {...register("password", {
                required: { value: true, message: " password" },
                minLength: {
                  value: 4,
                  message: "    4  10 ",
                },
                maxLength: {
                  value: 12,
                  message: "M    4  10 ",
                },
              })}
              placeholder="Password"
              error={errors.password}
            />
          </div>
          <Button type="submit" size="large">
            
          </Button>
        </form>

        <div className={styles.nav}>{nav}</div>
      </div>
    </div>
  );
};
