import React, { useEffect, useState } from "react";
import { Button, Input } from "../../components";
import styles from "./singInPage.module.css";
import { Link, useNavigate } from "react-router-dom";
import { LOGIN_ROUTE, USERS_ROUTE } from "../../utils/constans";
import { useForm } from "react-hook-form";
import cn from "classnames";
import { ReactComponent as CloseIcon } from "./close.svg";
import { useDispatch, useSelector } from "react-redux";
import { registrationUser } from "../../store/auth/authActionCreators";

export const SingInPage = () => {
  const [error, setError] = useState();
  const { auth } = useSelector((state) => state.auth);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const {
    register,
    handleSubmit,
    formState: { errors },
    reset,
  } = useForm();

  const onSubmit = (formData) => {
    if (formData.password !== formData.success_password) {
      setError("  ");
    } else {
      dispatch(registrationUser(formData));
    }
  };

  useEffect(() => {
    if (auth) {
      navigate(USERS_ROUTE);
    }
  }, [auth]);

  const nav = (
    <>
      <span>  ?</span> <Link to={LOGIN_ROUTE}></Link>
    </>
  );

  return (
    <div className={styles.wrapper}>
      <div className={styles.form_wrapper}>
        <h3 className={styles.title}></h3>
        <form onSubmit={handleSubmit(onSubmit)} className={styles.form}>
          <div className={styles.inputs}>
            <Input
              title=""
              type="text"
              {...register("name", {
                required: { value: true, message: " name" },
              })}
              placeholder=""
              error={errors.name}
            />
            <Input
              title=" "
              type="email"
              name="email"
              {...register("email", {
                required: { value: true, message: " email" },

                pattern: {
                  value:
                    /^[a-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\.[a-z0-9!#$%&'*+/=?^_`{|}~-]+)*@(?:[a-z0-9](?:[a-z0-9-]*[a-z0-9])?\.)+[a-z0-9](?:[a-z0-9-]*[a-z0-9])?$/,
                  message: " email ",
                },
              })}
              placeholder="Email"
              error={errors.email}
            />
            <Input
              title=""
              type="password"
              log="password1"
              hasIconHiding
              {...register("password", {
                required: { value: true, message: " password" },
                minLength: {
                  value: 4,
                  message: "    4  10 ",
                },
                maxLength: {
                  value: 12,
                  message: "    4  10 ",
                },
              })}
              placeholder=""
              error={errors.password}
            />
            <Input
              title=" "
              type="password"
              hasIconHiding
              {...register("success_password", {
                required: { value: true, message: " password" },
                minLength: {
                  value: 4,
                  message: "    4  10 ",
                },
                maxLength: {
                  value: 12,
                  message: "    4  10 ",
                },
              })}
              placeholder=" "
              error={errors.success_password}
            />
          </div>
          <Button type="submit" size="large">
            
          </Button>
        </form>

        <div className={styles.nav}>{nav}</div>

        {error && (
          <div className={cn(styles.error, styles.panel)} role="alert">
            {error}
            <button
              onClick={() => setError(undefined)}
              className={styles.close}
            >
              <CloseIcon />
            </button>
          </div>
        )}
      </div>
    </div>
  );
};
